.robotForm {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}

.robotForm>input {
  margin-bottom: 3px;
}

.robotForm>button {
  padding: 3px 0px;
  color: white;
  border: none;
  border-radius: 3px;
  background-color: rgb(130, 141, 238);
}

.robotForm>button:hover {
  background-color: rgb(188, 193, 240);
}

.error {
  color: red;
}
